body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.App {
  text-align: center;
  padding-right: 5%;
  padding-left: 5%
}

header {
  background-color:#707070;
  height: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color:white;
  padding: 10px;
 
}
.HeaderImage {
  -webkit-justify-content: center;
          justify-content: center;
}

.App-link {
  color: #61dafb;
}

.App-nav ul{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  list-style: none;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.App-nav ul li {
  padding: 10px;
  justify-items: end
}

.About-us {
  color:black;

  padding:20px;
}


.About-us p, h1 {
  font-weight:bold;
  text-align: left;
}

.App-build-plan {
  background-color:#D7DADF;
  padding:20px;

}
.Infofooter {
  background-color: #8391A4;
  padding:20px;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: 500
}

.card-group {
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
#map{
  height: 100vh
}
.card {
  margin: 10px;
  width:500px;
  height:auto;
}

.App-scenarios {
  background-color:#D7DADF;
  padding:20px;
  padding-bottom:50px;
}

.scenario-group {
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.scenario {
  background-color:white;
  border-radius:50%;
  width: 200px;
  height: 200px;
  line-height: 175px;
  text-align: center;
  border:10px solid black;
  cursor: pointer;
}

.scenario img {
  max-width:100%;
  max-height:100%;
  vertical-align: middle;
}

.information {
  background-color: #FFE7CC;
  padding:20px;
}






